body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
  
  /*
  -------------------------------------
  
  font
  
  --------------------------------------
  */
  
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
  @import url("https://cdn.jsdelivr.net/gh/sunn-us/SUIT/fonts/static/woff2/SUIT.css");
  @import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
  @import url('https://cdn.jsdelivr.net/npm/xeicon@2.3.3/xeicon.min.css');
  /*
  -------------------------------------
  
  reset
  
  --------------------------------------
  */
  
  html {
      box-sizing: border-box;
  }
  
  *,
  *:before,
  *:after {
      box-sizing: inherit;
      outline: none;
  }
  
  body {
      margin: 0;
      padding: 0;
      font-family: 'SUIT';
      font-weight: normal;
      line-height: 1;
      letter-spacing: -0.025em;
      color: #444444;
      word-break: keep-all;
  }
  
  ul,
  ol,
  li,
  dl,
  dt,
  dd {
      margin: 0;
      padding: 0;
      list-style: none;
  }
  
  p,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  figure {
      margin: 0;
      padding: 0;
      font-size: inherit;
      font-weight: inherit;
  }

  p{line-height: 1.45;}
  
  a {
      color: inherit;
      text-decoration: none;
  }
  
  img {
      border: 0;
      vertical-align: middle;
      font-size: 0;
      max-width: 100%;
  }
  
  table {
      border-collapse: collapse;
      border-spacing: 0;
      width: 100%;
  }
  
  select,
  input,
  textarea,
  button {
      font-size: inherit;
      font-family: 'SUIT';
      font-weight: inherit;
      margin: 0;
  }
  
  select,
  input,
  button {
      vertical-align: middle;
  }
  
  b,
  strong {
      font-weight: normal;
  }
  
  address,
  em,
  i {
      font-style: normal;
  }
  
  
  hr {
      margin: 0;
      padding: 0;
      border: none;
      display: block;
  }
  
  header,
  footer,
  article,
  section,
  aside,
  nav,
  main {
      display: block;
  }
  
  button,
  input,
  select,
  textarea {
      font-family: inherit;
      font-size: inherit;
      font-weight: inherit;
      margin: 0;
  }
  
  /* screen reader only */
  .sr-only,
  .hidden,
  .blind,
  .IR {
      position: absolute;
      width: 1px;
      height: 1px;
      padding: 0;
      margin: -1px;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      border: 0
  }
  
  /* Skip to content */
  .skip-to {
      position: absolute;
      top: -99px;
      left: 0;
      background: #333;
      color: #fff;
      width: 100%;
      padding: 10px 0;
      text-align: center;
      text-decoration: none;
      z-index: 999;
  }
  
  .skip-to:hover,
  .skip-to:focus,
  .skip-to:active {
      display: block;
      top: 0;
  }
  
  .row:after,
  .row:before {
      content: "";
      display: block;
  }
  
  .row:after {
      clear: both;
  }
  