$main-color: #032777;

$color-gray: #ddd;
$font-big: 20px;
$font-medium: 17px;
$font-small: 14px;

body { overflow-x: hidden; }
.Wrapper { min-width: 1170px; }
.inner { width: 1170px; margin: 0 auto; }
.Header {
  .top_bar {
    border-bottom: 1px solid $color-gray; font-size: $font-small; font-weight: 500;

    .inner {
      justify-content: flex-end; line-height: 30px;

      li { border-right: 1px solid $color-gray ; padding: 0 15px; }
    }
  }
  .inner {
    display: flex; justify-content: space-between;

    ul { display: flex; gap: 50px; line-height: 90px; font-size: 19px; font-weight: 500; }
    // 헤더 액티브 언더바 생성
    a {
      position: relative; display: block;

      &::after { content: ''; position: absolute; bottom: 25%; left: 50%; right: 0; width: 0; height: 2px; transform: translateX(-50%); background: $main-color; }
      &.active::after { width: 100%; transition: 0.5s; }
    }
  }
}